	module.exports = {"data":{"post":{"id":"/Users/lugang/My Github/LG-Blog-Gatsby/content/posts/2017-09-11--mac-fix/index.md absPath of file >>> MarkdownRemark","html":"<h2>Mac重装系统方法</h2>\n<h3>事出有因</h3>\n<hr>\n<p>年久失修是一方面，我想更主要的原因则是装的乱七八糟的程序太多了，这下一升级10.12，结果直接就卡死了，再重启就已经GG了。为啥我说主要的原因还是装的程序太多造成的呢？因为在升级系统之前Dock栏就一直保持在7月份左右的图标图案了，我后来删除了Fiddler（Mac下Charles更好），开机Dock没有刷新反而是变成了问号❓，还有其他一些种种的预示比如什么AI装不上啊的就不多说了。。。。</p>\n<p>总之重装好系统之后现在流畅得飞起，感觉机器又+1s了~</p>\n<p>这回重装比较与以往普通的重装又麻烦了挺多，废话不多说了，做个笔记下回抢救的时候还能知道怎么整。。。</p>\n<h3>崩溃导火索</h3>\n<hr>\n<p>升级最新版本。升级过程中卡死（等待了很久进度条也不动弹），只得强制重启，强制重启后会继续自动安装，无限死循环。</p>\n<h3>解决过程</h3>\n<hr>\n<p>一般Mac重装有三种方法：</p>\n<p><code class=\"language-text\">方法一：</code>TimeMachine。\n将备份盘连接到电脑后开机再选用备份盘里的备份恢复即可，这个方法最简单最有效，可以直接恢复到你当时备份的电脑的状态，真是一模一样，包括那时你开着什么程序打开了什么网页，就像时间机器一样，强得不行。</p>\n<p>但是，一个很严重的问题：一个懒癌选手没事会去备份吗？？看了看备份盘里17年2月的系统，还是咬了咬牙，换个方法吧。。。</p>\n<p><code class=\"language-text\">方法二：</code>找个U盘当系统盘重装。\n这个方法需要一个大于8g的U盘，然后去网上下载一个你想重新安装的Mac系统版本，具体的过程百度(在这种情况下google反而搜不到想要的结果。。)一下，过程还是也不复杂，但是这有一个查不到的坑！！！！就是你下载的版本不能比你RecoverHD的系统版本低。不然不管你怎么装都装不上，中途会冒出一个红色的🚫 宣告GG，因为我是升级新的系统过程中崩的，所以RecoverHD里记录的版本信息是10.12，比重装的系统10.11要高，现在想倒回10.11，这个方法就也不行了。。。还得再想其他办法。</p>\n<p><code class=\"language-text\">方法三：</code>网络重装。\n这个方法比较吃网速，需要连入wifi直接下载系统来进行重装，开机按住commond+shift+R 强制进行网络重装(不然又会自动安装安不上10.12)，直接系统网络重装后会按照机器的年份自动下载相应的系统版本，也就是重新装上出厂时的版本，之后进入系统再App Store下载10.11,下载完成后再重新安装10.11，最后问题终于得以解决，并且系统流畅得不行~</p>\n<h3>总结</h3>\n<hr>\n<p>首先，别给电脑瞎安软件，什么Angular、Xcode，不会的就不要装。\n其次，会的变成了曾经会的就赶紧卸，像什么React、Ae。\n最后，老老实实分个区给Timemachine，这样前面两条就不用管了~</p>","fields":{"slug":"/mac-fix/","prefix":"2017-09-11"},"frontmatter":{"title":"Mac拯救小记","author":"todaylg","category":"闲谈","cover":{"childImageSharp":{"resize":{"src":"/LG-Blog-Gatsby/static/bg-0f989bf59531c06d1be6221cfaa54c0a-ada8c.jpg"}}}}},"authornote":{"id":"/Users/lugang/My Github/LG-Blog-Gatsby/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>todaylg</strong> </p>"},"site":{"siteMetadata":{"facebook":{"appId":""}}}},"pathContext":{"slug":"/mac-fix/","prev":{"id":"/Users/lugang/My Github/LG-Blog-Gatsby/content/posts/2017-08-11--sass/index.md absPath of file >>> MarkdownRemark","fields":{"slug":"/sass/","prefix":"2017-08-11"},"frontmatter":{"title":"Sass基础","category":"基础备忘"}},"next":{"id":"/Users/lugang/My Github/LG-Blog-Gatsby/content/posts/2017-09-28--svg-outlineAnimation/index.md absPath of file >>> MarkdownRemark","fields":{"slug":"/svg-outlineAnimation/","prefix":"2017-09-28"},"frontmatter":{"title":"SVG动画制作小结","category":"小结"}}}}

